
import { defineComponent } from 'vue'
import SliderCard from '@/components/Slider/SliderCard.vue'

export default defineComponent({
  components: { SliderCard },
  props: {
    films: {
      type: Array,
      default: () => [],
      require: true
    },
    title: {
      type: String,
      default: '',
      require: true
    }
  },
  data() {
    return {
      currentSlide: 0 as number,
      canToggle: true as boolean
    }
  },
  methods: {
    nextSlide() {
      if (this.currentSlide < this.films.length - 3 && this.canToggle) {
        this.cantToggle()
        this.currentSlide++
      }
    },
    prevSlide() {
      if (this.currentSlide > 0 && this.canToggle) {
        this.cantToggle()
        this.currentSlide--
      }
    },
    cantToggle() {
      this.canToggle = false
      setTimeout(() => {
        this.canToggle = true
      }, 500)
    }
  }
})
